// global-components/header.scss
// import layout variables
@import '../layout/variables.scss';
.site-header {
    background-color: $dark;
    color: white;
    padding: 1rem;

    .site-branding {
        float: left;

        .site-title {
            font-size: 1.5rem;
            margin: 0;
            color: #fff;
        }
    }

    .main-navigation {
        float: right;

        #primary-menu {
            list-style: none;
            margin: 0;
            padding: 0;
            display: flex;

            li {
                margin-left: 1rem;
                position: relative; // Tambahkan posisi relatif untuk induk menu item

                a {
                    text-decoration: none;
                    color: #fff;
                    padding: 0.5rem 1rem;
                    transition: background-color 0.3s;

                    &:hover {
                        background-color: #555;
                    }
                }
                // Tambahkan gaya untuk submenu
                ul {
                    display: none; // Sembunyikan submenu
                    position: absolute;
                    top: 100%;
                    left: 0;
                    background-color: $dark;
                    padding: 0;
                    list-style: none;
                    margin: 0;

                    li {
                        margin-left: 0;

                        a {
                            padding: 0.5rem 1rem;
                            width: 100%;
                            display: block;

                            &:hover {
                                background-color: #777;
                            }
                        }
                    }
                }
                &:hover > ul {
                    display: block; // Tampilkan submenu saat induk di-hover
                }
            }
        }
    }

    // Clear floats
    &::after {
        content: "";
        display: table;
        clear: both;
    }
}

// Responsif untuk layar kecil
@media (max-width: 768px) {
    .site-header {
        .site-branding {
            float: none;
            text-align: center;
            margin-bottom: 1rem;
        }

        .main-navigation {
            float: none;
            text-align: center;

            #primary-menu {
                flex-direction: column;

                li {
                    margin: 0;

                    a {
                        padding: 1rem;
                    }
                }
            }
        }
    }
}
